<template>
<div>
  <div class="element-page-container">
    <h1 style="font-size:7em; margin:0em;">{{this.name}}</h1>
    <h1>coming soon</h1>
    <!-- to be done -->
  </div>
  <div class="element-page-footer">
    <button @click="click" class="return-button">Go back to periodic table</button>
  </div>
</div>
</template>

<script>
import ElementProperties from '@/assets/json/ElementProperties.js'

export default {
  name: 'ElementPage',
  methods: {
    click() {
      this.$router.push('/chemistry/PeriodicTable/0');
    }
  },
  mounted() {
    this.name = ElementProperties[this.AtomicNumber]["Name"]
  },
  data() {
    return {
      name: "",  
    }
  },
  props: {
    AtomicNumber: {type: Number, required: true},
  }
}

</script>