<template>
  <div>
    <select name="Sorting" id="sorting" @change="onChange($event)">
      <option value="n" default>
        No Sort
      </option>
      <option value="bp">Boiling point</option>
      <option value="aw">Atomic Weight</option>
      <option value="mp">Melting Point</option>
      <option value="sh">Specific heat capacity</option>
      <option value="en">Electro-negativity</option>
      <option value="ea">Electron Affinity</option>
      <option value="ab">Abundance in Earth's crust</option>
      <option value="de">Density</option>
      <option value="ar">Atomic Radius</option>
      <option value="ie">Ionization Energy</option>
      <option value="gb">Group Block</option>
      <option value="cb">Chemical block</option>
      <option value="cp">CPK Hex-Color</option>
      <option value="ss">Standard State</option>
      <option value="yd">Year Discovered</option>
      <option value="or">Origin</option>
    </select>
  </div>  
</template>

<script>

export default {
  name: 'Selector',
  // data () {
  //   return 
  //   }
  // },
  methods: {
    onChange(event) {
      let selectData = {
    cb: { name: "Block", max: "none", min: "none", nname: "Chemical block" },
    bp: { name: "BoilingPoint", max: 5869, min: 4.22, nname: "Boiling Point" },
    aw: { name: "AtomicMass", max: 295.216, min: 1.0079, nname: "Atomic Mass" },
    mp: { name: "MeltingPoint", max: 4000, min: 14.01, nname: "Melting Point" },
    yd: { name: "YearDiscovered", max: 2010, min: 1669, nname: "Year Discovered" },
    sh: {
      name: "SpecificHeatCapacity",
      max: Math.log(14.304),
      min: Math.log(0.094),
      nname: "Specific Heat Capacity",
    },
    en: {
      name: "Electronegativity",
      max: 3.98,
      min: 0.7,
      nname: "Electro-negativity (Pauling scale)",
    },
    ab: {
      name: "AbundanceInEarth'sCrust",
      max: Math.log(461000),
      min: Math.log(3e-20),
      nname: "Abundance in Earth's crust",
    },
    n: { name: "none", max: "none", min: "none", nname: "Element Name" },
    ea: {
      name: "ElectronAffinity",
      max: 3.617,
      min: 0.079,
      nname: "Electron Affinity",
    },
    de: {
      name: "Density",
      max: 22.57,
      min: 0.0008999,
      nname: "Density",
    },
    ar: {
      name: "AtomicRadius",
      max: 348.0,
      min: 135,
      nname: "Atomic Radius",
    },
    ie: {
      name: "IonizationEnergy",
      max: Math.log(24.587),
      min: Math.log(3.894),
      nname: "Ionization Energy",
    },
    gb: {
      name: "GroupBlock",
      max: "none",
      mine: "none",
      nname: "Group Block",
    },
    cp: {
      name: "CPKHexColor",
      max: "none",
      min: "none",
      nname: "CPK Hex-Color",
    },
    ss: {
      name: "StandardState",
      max: "none",
      min: "none",
      nname: "Standard State",
    },
    or: {
      name: "Origin",
      max: "none",
      min: "none",
      nname: "Origin",
    },
  }
      this.$store.commit('setSelect', {Data: selectData[event.target.value], sort_type: event.target.value})
    },
  },
}

</script>