<template>
  <div>
    <button v-if="this.el1 != '0'" @click="click()" class="Element" :style="setColor">
      <div>
        <div class="Element-name">
          <p class="atN">{{ this.atomicNumber }}</p>
          <p class="eS">{{ this.symbol }}</p>
          <p class="eN" :style="style">{{ setName }}</p>
        </div>
      </div>
    </button>
  </div>
</template>

<script>

import ElementProperties from '@/assets/json/ElementProperties.js'

export default {
  name: "Element",
  mounted() {
    if(this.el1 !== "0" && this.el1 !== "*" && this.el1 !== "**") {
      this.name = ElementProperties[this.el1]["Name"] // gets the element's name
      this.atomicNumber = this.el1 // gets the element's atomic number
      this.symbol = this.data[0] // gets the element's symbol
    } else if(this.el1 !== "0"){
      this.symbol = this.data[0]
    }
  },
  methods: {
    click() {
      if(this.el1 != "*" && this.el1 != "**") {
        this.$router.push('/chemistry/PeriodicTable/'+this.el1);
      }
    },
    lerp1(valueToInterpolate, max, min, max1, min1) { // leanear interpoltion of valueToInterpolate 
        let m = (max1 - min1) / (max - min)
        let k = max - valueToInterpolate
        return max1 - (k * m)
    },
    lerp2(valueToInterpolate, max, min, max1, min1, ab = 'no') {
        let m = (max1 - min1) / (max - min)
        let k = max - Math.log(valueToInterpolate)
        if(ab == 'no') {
          return max1 - (k * m)
        } else {
          return min1 + (k * m)
        }
    },
  },
  computed: {
    setColor() { // determines the color
      if(this.el1 === "0") { // checks if it is element or not
        return "background-color:#fff;"
      }  
      
      else if(this.el1 === "*" || this.el1 === "**") { // checks if it is * or **
        return "background-color:#fff;"
      }
      let el1 = this.el1
      
      const selectData = this.$store.getters.select // gets the selection data
      const sort = selectData["sort_type"]
      let colorOfElement = 'rgb(239, 239, 239)' // initializes the color to default color
      let lerpmax = 360 // definning the maximum for all lerp functions
      let lerpmin = 240 // definning the minimum for all lerp functions

      if(sort === "n") {
        colorOfElement = 'hsla(206, 82%, 64%, 1)' // sets the color to no sort color
      } else if(sort === "cp") { // checks if the sort type is CPKHexColor

        const cpcolorval = ElementProperties[el1]["CPKHexColor"] // finds the CPKHexColor of the element
        
        if(cpcolorval !== "-") { // checks if it is a color or if it is undefined
          colorOfElement = "#"+cpcolorval // sets the color for defined CPKHexColor
        }
      } else if(sort === "gb") { // checks if the sort type is Group Block
        const groupBlockName = ElementProperties[el1]["GroupBlock"] // finds the GroupBlock of the element
        if(groupBlockName === "Transition metal") {
          colorOfElement = "hsla(178, 39%, 72%, 1)" // sets the color to transition metal
        } else if(groupBlockName === "Metalloid") {
          colorOfElement = "hsla(68, 100%, 35%, 0.59)"
        } else if(groupBlockName === "Nonmetal") {
          colorOfElement = "hsla(58, 247%, 50%, 0.59)"
        } else if(groupBlockName === "Halogen") {
          colorOfElement = "hsla(78, 77%, 57%, 0.59)"
        } else if(groupBlockName === "Noble gas") {
          colorOfElement = "hsla(37, 68%, 55%, 0.58)"
        } else if(groupBlockName === "Post-transition metal") {
          colorOfElement = "hsla(93, 64%, 57%,1)"
        } else if(groupBlockName === "Alkali metal") {
          colorOfElement = "hsla(265, 100%, 84%, 0.95)"
        } else if (groupBlockName === "Alkaline earth metal") {
          colorOfElement = "hsla(327, 47%, 75%, 0.95)"
        } else if (groupBlockName === "Lanthanide") {
          colorOfElement = "hsla(180, 100%, 75%, 0.64)"
        } else if(groupBlockName === "Actinide") {
          colorOfElement = "hsla(172, 77%, 64%, 1)"
        }
      } else if(sort === "ss") { // checks if the sort type is Standard State
        const standardState = ElementProperties[el1]["StandardState"] // finds the StandardState of the element
        if(standardState === "Gas") {
          colorOfElement = "hsla(37, 68%, 55%, 0.58)"
        } else if(standardState === "Liquid") {
          colorOfElement = "hsla(93, 64%, 57%, 1)"
        } else if(standardState === "Solid") {
          colorOfElement = "hsla(178, 39%, 72%, 1)"
        } else if(standardState === "Expected to be a Solid") {
          colorOfElement = "hsla(69, 49%, 71%, 1)"
        } else if(this.symbol === "Og") {
          colorOfElement = "hsla(220, 50%, 71%, 1)"
        }
      } else if(sort === "or") { // checks if the sort type is Origin
        const originName = ElementProperties[el1]["Origin"] // finds the Origin of the element
        if(originName === "Primordial") {
          colorOfElement = "hsla(178,40%,72%,1 )"          
        } else if (originName === "From Decay") {
          colorOfElement = "hsla(68, 100%, 35%, 0.59)"
        } else if (originName === "Synthetic") {
          colorOfElement = "hsla(265, 100%, 84%, 0.95)"
        }
      } else if(sort === "cb") { // checks if the sort type is Chemical Block
        const chemicalBlockName = ElementProperties[el1]["Block"] // finds the ChemicalBlock of the element
        if(chemicalBlockName === "s-block") {
          colorOfElement = "hsla(178, 40%, 72%, 1)"
        } else if(chemicalBlockName === "d-block") {
          colorOfElement = "hsla(78, 77%, 57%, 0.59)"
        } else if(chemicalBlockName === "p-block") {
          colorOfElement = "hsla(100, 60%, 49%, 0.59)"
        } else if(chemicalBlockName === "f-block") {
          colorOfElement = "hsla(172, 77%, 64%)"
        }
      } else if(sort === "bp") { // checks if the sort type is Boiling point
        const boilingPointVal = ElementProperties[el1]["BoilingPoint"] // finds the BoilingPoint of the element
        const maxBoilingPointVal = selectData["Data"]["max"]
        const minBoilingPointVal = selectData["Data"]["min"]
        if(boilingPointVal !== "-") {
          let newVal = this.lerp1(parseFloat(boilingPointVal), parseFloat(maxBoilingPointVal), parseFloat(minBoilingPointVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "mp") { // checks if the sort type is Melting point
        let meltingPointVal = ElementProperties[el1]["MeltingPoint"] // finds the MeltingPoint of the element
        const maxMeltingPointVal = selectData["Data"]["max"]
        const minMeltingPointVal = selectData["Data"]["min"]
        if(meltingPointVal !== "-") {
          let newVal = this.lerp1(parseFloat(meltingPointVal), parseFloat(maxMeltingPointVal), parseFloat(minMeltingPointVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "aw") { // checks if the sort type is Atomic Weight
        const atomicWeightVal = ElementProperties[el1]["AtomicMass"] // finds the AtomicWeight of the element
        const maxAtomicWeightVal = selectData["Data"]["max"]
        const minAtomicWeightVal = selectData["Data"]["min"]
        if(atomicWeightVal !== "-") {
          let newVal = this.lerp1(parseFloat(atomicWeightVal), parseFloat(maxAtomicWeightVal), parseFloat(minAtomicWeightVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "de") { // checks if the sort type is Density
        const densityVal = ElementProperties[el1]["Density"] // finds the Density of the element
        const maxDensityVal = selectData["Data"]["max"]
        const minDensityVal = selectData["Data"]["min"]
        if(densityVal !== "-") {
          let newVal = this.lerp1(parseFloat(densityVal), parseFloat(maxDensityVal), parseFloat(minDensityVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "en") { // checks if the sort type is Electronegativity
        const electronegativityVal = ElementProperties[el1]["Electronegativity"] // finds the Electronegativity of the element
        const maxElectronegativityVal = selectData["Data"]["max"]
        const minElectronegativityVal = selectData["Data"]["min"]
        if(electronegativityVal !== "-") {
          let newVal = this.lerp1(parseFloat(electronegativityVal), parseFloat(maxElectronegativityVal), parseFloat(minElectronegativityVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "sh") { // checks if the sort type is Specific Heat Capacity
        const specificHeatCapacityVal = ElementProperties[el1]["SpecificHeatCapacity"] // finds the SpecificHeatCapacity of the element
        const maxSpecificHeatCapacityVal = selectData["Data"]["max"]
        const minSpecificHeatCapacityVal = selectData["Data"]["min"]
        if(specificHeatCapacityVal !== "-") {
          let newVal = this.lerp1(Math.log10(parseFloat(specificHeatCapacityVal)), parseFloat(maxSpecificHeatCapacityVal), parseFloat(minSpecificHeatCapacityVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "yd") { // checks if the sort type is Year Discovered
        const yearDiscoveredVal = ElementProperties[el1]["YearDiscovered"] // finds the YearDiscovered of the element
        if(yearDiscoveredVal === "Ancient") {
          colorOfElement = "hsla(200, 86%, 50%, 1)"
        } else {
          const maxYearDiscoveredVal = selectData["Data"]["max"]
          const minYearDiscoveredVal = selectData["Data"]["min"]
          
          let newVal = this.lerp1(parseFloat(yearDiscoveredVal), parseFloat(maxYearDiscoveredVal), parseFloat(minYearDiscoveredVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "ea") { // checks if the sort type is Electron Affinity
        const electronAffinityVal = ElementProperties[el1]["ElectronAffinity"] // finds the ElectronAffinity of the element
        const maxElectronAffinityVal = selectData["Data"]["max"]
        const minElectronAffinityVal = selectData["Data"]["min"]
        if(electronAffinityVal !== "-") {
          let newVal = this.lerp1(parseFloat(electronAffinityVal), parseFloat(maxElectronAffinityVal), parseFloat(minElectronAffinityVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "ab") { // checks if the sort type is Abundance in Earth's crust'
        const abundanceVal = ElementProperties[el1]["AbundanceInEarth'sCrust"] // finds the Abundance of the element
        const maxAbundanceVal = selectData["Data"]["max"]
        const minAbundanceVal = selectData["Data"]["min"]
        if(abundanceVal != "-") {
          let newVal = this.lerp2(parseFloat(abundanceVal), parseFloat(maxAbundanceVal), parseFloat(minAbundanceVal), lerpmax, lerpmin, 'yes')
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "ar") { // checks if the sort type is Atomic Radius
        const atomicRadiusVal = ElementProperties[el1]["AtomicRadius"] // finds the AtomicRadius of the element
        const maxAtomicRadiusVal = selectData["Data"]["max"]
        const minAtomicRadiusVal = selectData["Data"]["min"]
        if(atomicRadiusVal !== "-") {
          let newVal = this.lerp1(parseFloat(atomicRadiusVal), parseFloat(maxAtomicRadiusVal), parseFloat(minAtomicRadiusVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      } else if(sort === "ie") { // checks if the sort type is Ionization Energy
        const ionizationEnergyVal = ElementProperties[el1]["IonizationEnergy"] // finds the IonizationEnergy of the element
        const maxIonizationEnergyVal = selectData["Data"]["max"]
        const minIonizationEnergyVal = selectData["Data"]["min"]
        if(ionizationEnergyVal !== "-") {
          let newVal = this.lerp2(parseFloat(ionizationEnergyVal), parseFloat(maxIonizationEnergyVal), parseFloat(minIonizationEnergyVal), lerpmax, lerpmin)
          colorOfElement = "hsla("+Math.round(newVal)+", 86%, 50%, 1)"
        }
      }
      let substring = colorOfElement.substring(5, 8)
      if(substring.indexOf(',') <= -1 && ['cp', 'or', 'ss', 'cb', 'gb', 'n'].includes(sort) === false) {
        if(Number(substring) < 260 && Number(substring) >= 240) {
          return "background-color:"+colorOfElement+";color:hsla(360, 0%, 90%, 1);"
        }
      }
      return "background-color:"+colorOfElement+";"
    },
    setName(){
      if(this.el1 != "0" && this.el1 != "*" && this.el1 != "**") {
        let findElementProperties = this.$store.getters.select["Data"]["name"]
        if(this.$store.getters.select["Data"]["name"] == "none") {
          findElementProperties =  "Name"
        }
        
        const returnprop = ElementProperties[this.el1][findElementProperties]
        
        if(returnprop === "-") {
          return "Undefined"
        } else {return returnprop}
      }
      return ""
    },
    style() {
      let style = ""
      let name = this.setName
      if(name.length > 15){
          style = "font-size:0.7em;"
      }
      return style
    }
  },
  data() {
    return {
      name: "",
      atomicNumber: "",
      symbol: "",
      el1: this.data[1]
    }
  },
  props: {
    data: {
      type: Array,
      required: true
    }
  },
}

</script>