import { createStore } from "vuex";

export default createStore({
  state: {
    select: {Data: {name: 'none', max: 'none', min: 'none', nname: 'No Sort'}, sort_type: "n"},
  },
  mutations: {
    setSelect(state, select) {
      state.select = select;
    }
  },
  actions: {},
  modules: {},
  getters: {
    select: state => state.select,
  },
});
