<template>
<div>
  <div class="pt"><Element v-for="(el, idx) in elements" :key="idx" :data="el"></Element>
    <div class="legend1" :style="styleOflegend1">
      <p class="atN-legend1">Atomic Number</p>
      <p class="eS-legend1">Symbol</p>
      <p class="eN-legend1">{{selectedData["Data"]["nname"]}}</p>
    </div>
    <div class="legend2">
      <div class="gradient-legend2" v-if="selectedData['Data']['max'] != 'none'" :style="setColor"><p style="margin:0; padding-top:0.4em;">{{max}}</p><p style="padding-top:7em; margin:0; color:white;">{{min}}</p></div>
    </div>
    <div class="pt" style="grid-template-columns:repeat(17,5em); margin-top:2em;"><Element v-for="(el, idx) in elements2" :key="idx" :data="el"></Element>
    <Element v-for="(el, idx) in elements3" :key="idx" :data="el"></Element></div>
  </div>
</div>
</template>

<script>

import Element from './Element.vue'

export default {
  name: 'PeriodicTable',
  components: {
    Element
  },
  computed: {
    selectedData: function() {
      return this.$store.state.select
    },
    setColor: function() { // sets the background color of legend2 (gradient)
      let color = "background: linear-gradient(#f29d11, #9198e5);"
      const max = this.selectedData['Data']['max']
      const min = this.selectedData['Data']['min']
      const sort = this.selectedData["sort_type"]
      console.log(sort)
      let lerpmax = 360
      let lerpmin = 240
      if(sort === "bp" || sort === "mp" || sort === "en" || sort === "aw" || sort === "ea" || sort === "de" || sort === "ar" || sort === "yd" || sort === "sh" || sort === "ab" || sort === "ie") {	
        color = "background: linear-gradient(hsl("+this.lerp1(max, max, min, lerpmax, lerpmin)+", 86%, 50%), hsl("+this.lerp1(min, max, min, lerpmax, lerpmin)+", 86%, 50%));"
      }
      console.log(color)
      return color
    },
    max: function() { // returns the maximum value of the property, corrects for logarithmic scale
      if (this.selectedData['sort_type'] === 'sh' || this.selectedData['sort_type'] === 'ir' || this.selectedData['sort_type'] === 'ab') {
        return parseFloat((Math.pow(Math.E, this.selectedData['Data']['max'])).toPrecision(5))
      }
      return parseFloat(this.selectedData['Data']['max'].toPrecision(5))
    },
    min: function() { // does the same thing as the max function but for min
      if (this.selectedData['sort_type'] === 'sh' || this.selectedData['sort_type'] === 'ir' || this.selectedData['sort_type'] === 'ab') {
        return parseFloat((Math.pow(Math.E, this.selectedData['Data']['min'])).toPrecision(5))
      }
      return parseFloat(this.selectedData['Data']['min'].toPrecision(5))
    },
    styleOflegend1() { // sets the background color of legend1, using a lookup table
      const sort = this.selectedData["sort_type"]	
      let color = "background-color:hsla(206, 82%, 64%, 1);"
      if(sort === "bp" || sort === "aw") {
        color = "background-color:hsla(240, 86%, 50%, 1);color:hsla(360, 0%, 90%, 1);"
      } else if(sort === "mp") {
        color = "background-color:hsla(240, 86%, 50%, 1);color:hsla(360, 0%, 90%, 1);"
      } else if(sort === "sh") {
        color = "background-color:hsla(360, 86%, 50%, 1);"
      } else if(sort === "en") {
        color = "background-color:hsla(295, 86%, 50%, 1);"
      } else if(sort === "ea") {
        color = "background-color:hsla(263, 86%, 50%, 1);"
      } else if(sort === "ab") {
        color = "background-color:hsla(252, 86%, 50%, 1);color:hsla(360, 0%, 90%, 1);"
      } else if(sort === "de") {
        color = "background-color:hsla(240, 86%, 50%, 1);color:hsla(360, 0%, 90%, 1);"
      } else if(sort === "ar") {
        color = "background-color:hsla(232, 86%, 50%, 1);color:hsla(360, 0%, 90%, 1);"
      } else if(sort === "ie") {
        color = "background-color:hsla(321, 86%, 50%, 1)"
      } else if(sort === "gb") {
        color = "background-color:hsla(58, 247%, 50%, 0.59);"
      } else if(sort === "cb") {
        color = "background-color:hsla(58, 247%, 50%, 0.59);"
      } else if(sort === "ss") {
        color = "background-color:hsla(37, 68%, 55%, 0.58);"
      } else if(sort === "yd") {
        color = "background-color:hsla(274, 86%, 50%, 1);"
      } else if(sort === "or") {
        color = "background-color:hsla(178,40%,72%,1 );"
      }
      return color
    }
  },
